// src/pages/DashboardPage.tsx - 

import React from 'react';
import { gql, useQuery } from '@apollo/client';
import { useAuth } from '../contexts/AuthContext';
import { MainLayout } from '../layouts/MainLayout';

// GraphQL
const GET_SCHEMA_QUERY = gql`
  query GetSchema {
    __schema {
      queryType {
        name
      }
    }
  }
`;

export const DashboardPage: React.FC = () => {
    const { user, logout, isLoading: authLoading } = useAuth();
    const { data, loading, error } = useQuery(GET_SCHEMA_QUERY);

    const handleLogout = async () => {
        try {
            await logout();
        } catch (error) {
            console.error(':', error);
        }
    };

    if (authLoading) {
        return (
            <MainLayout>
                <div style={{ textAlign: 'center', padding: '20px' }}>
                    <p> ...</p>
                </div>
            </MainLayout>
        );
    }

    return (
        <MainLayout>
            <div style={{ padding: '20px' }}>
                {/*  */}
                <header style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginBottom: '30px',
                    padding: '0 0 20px 0',
                    borderBottom: '1px solid #ddd',
                }}>
                    <div>
                        <h1 style={{ margin: '0 0 8px 0' }}></h1>
                        <p style={{ margin: '0', color: '#666' }}>
                            {user?.username}  ({user?.role})
                        </p>
                    </div>
                    <button
                        onClick={handleLogout}
                        style={{
                            padding: '8px 16px',
                            backgroundColor: '#dc3545',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            cursor: 'pointer',
                            fontSize: '14px',
                        }}
                    >
                        
                    </button>
                </header>

                {/*  */}
                <div style={{
                    padding: '16px',
                    marginBottom: '20px',
                    backgroundColor: '#d4edda',
                    border: '1px solid #c3e6cb',
                    borderRadius: '4px',
                    color: '#155724',
                }}>
                    <h3 style={{ margin: '0 0 8px 0' }}> HttpOnly Cookie!</h3>
                    <p style={{ margin: '0' }}>
                        
                        DevTools  Application  Cookies  auth_token 
                    </p>
                </div>

                {/*  */}
                <div style={{
                    padding: '20px',
                    border: '1px solid #ddd',
                    borderRadius: '8px',
                    marginBottom: '20px',
                    backgroundColor: '#f8f9fa',
                }}>
                    <h3 style={{ margin: '0 0 16px 0' }}> </h3>
                    <div style={{ display: 'grid', gridTemplateColumns: '120px 1fr', gap: '8px' }}>
                        <strong>ID:</strong> <span>{user?.id}</span>
                        <strong>:</strong> <span>{user?.username}</span>
                        <strong>:</strong> <span>{user?.role}</span>
                        <strong>:</strong> <span>{user?.createdAt ? new Date(user.createdAt).toLocaleString() : '-'}</span>
                        <strong>:</strong> <span>{user?.lastLoginAt ? new Date(user.lastLoginAt).toLocaleString() : '-'}</span>
                    </div>
                </div>

                {/* GraphQL */}
                <div style={{
                    padding: '20px',
                    border: '1px solid #ddd',
                    borderRadius: '8px',
                    marginBottom: '20px',
                }}>
                    <h3 style={{ margin: '0 0 16px 0' }}> GraphQL</h3>
                    {loading && <p> GraphQL...</p>}
                    {error && <p style={{ color: '#dc3545' }}> : {error.message}</p>}
                    {data && (
                        <p style={{ color: '#28a745' }}>
                             GraphQL! Query Type: {data.__schema.queryType.name}
                        </p>
                    )}
                </div>

                {/*  */}
                <div style={{
                    padding: '20px',
                    border: '1px solid #ddd',
                    borderRadius: '8px',
                    backgroundColor: '#e3f2fd',
                }}>
                    <h3 style={{ margin: '0 0 16px 0' }}> </h3>
                    <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: '12px' }}>
                        <div>
                            <h4 style={{ margin: '0 0 8px 0', color: '#1976d2' }}> </h4>
                            <ul style={{ margin: '0', paddingLeft: '20px' }}>
                                <li>HttpOnly Cookie</li>
                                <li>JWT </li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                        <div>
                            <h4 style={{ margin: '0 0 8px 0', color: '#1976d2' }}> </h4>
                            <ul style={{ margin: '0', paddingLeft: '20px' }}>
                                <li>React + TypeScript</li>
                                <li>Apollo Client</li>
                                <li>GraphQL Code Generator</li>
                                <li></li>
                            </ul>
                        </div>
                        <div>
                            <h4 style={{ margin: '0 0 8px 0', color: '#1976d2' }}> React Router</h4>
                            <ul style={{ margin: '0', paddingLeft: '20px' }}>
                                <li>URL-based routing</li>
                                <li></li>
                                <li>Page-based </li>
                                <li></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </MainLayout>
    );
};