// src/contexts/AuthContext.tsx

import React, { createContext, useContext, useReducer, useEffect } from 'react';
import { useMutation, useLazyQuery } from '@apollo/client';
import {
    AuthState,
    AuthContextType,
    LoginCredentials,
    AuthUser
} from '../types/auth';
import {
    LoginDocument,
    LogoutDocument,
    MeDocument
} from '../generated/graphql';

// 
const initialState: AuthState = {
    isAuthenticated: false,
    isLoading: true,
    user: null,
    error: null,
};

// 
type AuthAction =
    | { type: 'AUTH_START' }
    | { type: 'AUTH_SUCCESS'; payload: AuthUser }
    | { type: 'AUTH_FAILURE'; payload: string }
    | { type: 'AUTH_LOGOUT' }
    | { type: 'AUTH_RESET_ERROR' }
    | { type: 'AUTH_SET_LOADING'; payload: boolean };

// Reducer
const authReducer = (state: AuthState, action: AuthAction): AuthState => {
    switch (action.type) {
        case 'AUTH_START':
            return {
                ...state,
                isLoading: true,
                error: null,
            };

        case 'AUTH_SUCCESS':
            return {
                ...state,
                isAuthenticated: true,
                isLoading: false,
                user: action.payload,
                error: null,
            };

        case 'AUTH_FAILURE':
            return {
                ...state,
                isAuthenticated: false,
                isLoading: false,
                user: null,
                error: action.payload,
            };

        case 'AUTH_LOGOUT':
            return {
                ...state,
                isAuthenticated: false,
                isLoading: false,
                user: null,
                error: null,
            };

        case 'AUTH_RESET_ERROR':
            return {
                ...state,
                error: null,
            };

        case 'AUTH_SET_LOADING':
            return {
                ...state,
                isLoading: action.payload,
            };

        default:
            return state;
    }
};

// 
const AuthContext = createContext<AuthContextType | undefined>(undefined);

// 
export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [state, dispatch] = useReducer(authReducer, initialState);

    // GraphQL
    const [loginMutation] = useMutation(LoginDocument);
    const [logoutMutation] = useMutation(LogoutDocument);
    const [checkMe, { data: meData }] = useLazyQuery(MeDocument, {
        errorPolicy: 'ignore', // 
    });

    // 
    const login = async (credentials: LoginCredentials): Promise<void> => {
        try {
            dispatch({ type: 'AUTH_START' });

            const { data } = await loginMutation({
                variables: {
                    username: credentials.username,
                    password: credentials.password,
                },
            });

            if (data?.login?.user) {
                // Cookie
                dispatch({
                    type: 'AUTH_SUCCESS',
                    payload: data.login.user as AuthUser
                });
            } else {
                throw new Error('');
            }
        } catch (error) {
            const errorMessage = error instanceof Error ? error.message : '';
            dispatch({ type: 'AUTH_FAILURE', payload: errorMessage });
            throw error;
        }
    };

    // 
    // 
    const logout = async (): Promise<void> => {
        try {
            // Cookie
            const { data } = await logoutMutation({
                variables: {
                    refreshToken: '', // Cookie
                },
            });

            // Boolean
            if (data?.logout === true) {
                console.log('');
            }

            dispatch({ type: 'AUTH_LOGOUT' });
        } catch (error) {
            console.error(':', error);
            // 
            dispatch({ type: 'AUTH_LOGOUT' });
        }
    };

    // 
    const resetAuthError = (): void => {
        dispatch({ type: 'AUTH_RESET_ERROR' });
    };

    // Cookie
    const checkAuthStatus = async (): Promise<void> => {
        try {
            dispatch({ type: 'AUTH_SET_LOADING', payload: true });

            const result = await checkMe({
                fetchPolicy: 'network-only',  // 
                errorPolicy: 'ignore'         // 
            });

            if (result.data?.me) {
                dispatch({
                    type: 'AUTH_SUCCESS',
                    payload: result.data.me as AuthUser
                });
            } else {
                dispatch({ type: 'AUTH_LOGOUT' });
            }
        } catch (error) {
            console.error(':', error);
            dispatch({ type: 'AUTH_LOGOUT' });
        } finally {
            //  
            dispatch({ type: 'AUTH_SET_LOADING', payload: false });
        }
    };

    // 
    useEffect(() => {
        checkAuthStatus();
    }, []);

    // meData
    useEffect(() => {
        if (meData?.me) {
            dispatch({
                type: 'AUTH_SUCCESS',
                payload: meData.me as AuthUser
            });
        }
    }, [meData]);

    const contextValue: AuthContextType = {
        ...state,
        login,
        logout,
        resetAuthError,
        checkAuthStatus,
    };

    return (
        <AuthContext.Provider value={contextValue}>
            {children}
        </AuthContext.Provider>
    );
};

// 
export const useAuth = (): AuthContextType => {
    const context = useContext(AuthContext);
    if (context === undefined) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
};