// src/pages/LoginPage.tsx - Page-based 100%

import React, { useState } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { LoginCredentials } from '../types/auth';

interface LocationState {
    from?: Location;
}

export const LoginPage: React.FC = () => {
    // LoginPage.tsx
    const { isAuthenticated, isLoading } = useAuth();
    const location = useLocation();
    const state = location.state as LocationState;

    // LoginForm.tsx
    const { login, error, resetAuthError } = useAuth();
    const [credentials, setCredentials] = useState<LoginCredentials>({
        username: '',
        password: '',
    });
    const [validationErrors, setValidationErrors] = useState<{
        username?: string;
        password?: string;
    }>({});

    // LoginForm.tsx

    // 
    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setCredentials(prev => ({
            ...prev,
            [name]: value,
        }));

        // 
        if (validationErrors[name as keyof typeof validationErrors]) {
            setValidationErrors(prev => ({
                ...prev,
                [name]: undefined,
            }));
        }

        // 
        if (error) {
            resetAuthError();
        }
    };

    // 
    const validateForm = (): boolean => {
        const errors: typeof validationErrors = {};

        if (!credentials.username.trim()) {
            errors.username = '';
        }

        if (!credentials.password.trim()) {
            errors.password = '';
        } else if (credentials.password.length < 6) {
            errors.password = '6';
        }

        setValidationErrors(errors);
        return Object.keys(errors).length === 0;
    };

    // 
    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        if (!validateForm()) {
            return;
        }

        try {
            await login(credentials);
            // 
        } catch (error) {
            // 
        }
    };

    // Enter
    const handleKeyDown = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter' && !isLoading) {
            handleSubmit(e as any);
        }
    };

    // LoginPage.tsx

    // 
    if (isLoading) {
        return (
            <div style={{
                minHeight: '100vh',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: '#f5f5f5',
                flexDirection: 'column',
                gap: '16px'
            }}>
                <div style={{ fontSize: '24px' }}></div>
                <p>...</p>
            </div>
        );
    }

    // 
    if (isAuthenticated) {
        const from = state?.from?.pathname || '/';
        return <Navigate to={from} replace />;
    }

    // LoginForm.tsx
    return (
        <div style={{
            minHeight: '100vh',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            backgroundColor: '#f5f5f5',
            padding: '20px'
        }}>
            {/*  */}
            <div style={{
                maxWidth: '400px',
                margin: '0 auto',
                padding: '20px',
                border: '1px solid #ddd',
                borderRadius: '8px',
                backgroundColor: '#f9f9f9'
            }}>
                <h2 style={{ textAlign: 'center', marginBottom: '24px' }}>
                    
                </h2>

                <form onSubmit={handleSubmit}>
                    {/*  */}
                    <div style={{ marginBottom: '16px' }}>
                        <label htmlFor="username" style={{
                            display: 'block',
                            marginBottom: '4px',
                            fontWeight: 'bold'
                        }}>
                            
                        </label>
                        <input
                            type="text"
                            id="username"
                            name="username"
                            value={credentials.username}
                            onChange={handleInputChange}
                            onKeyDown={handleKeyDown}
                            disabled={isLoading}
                            style={{
                                width: '100%',
                                padding: '8px 12px',
                                border: `1px solid ${validationErrors.username ? '#ff6b6b' : '#ddd'}`,
                                borderRadius: '4px',
                                fontSize: '14px',
                                backgroundColor: isLoading ? '#f5f5f5' : 'white',
                            }}
                            placeholder=""
                            autoComplete="username"
                            autoFocus
                        />
                        {validationErrors.username && (
                            <p style={{ color: '#ff6b6b', fontSize: '12px', margin: '4px 0 0 0' }}>
                                {validationErrors.username}
                            </p>
                        )}
                    </div>

                    {/*  */}
                    <div style={{ marginBottom: '16px' }}>
                        <label htmlFor="password" style={{
                            display: 'block',
                            marginBottom: '4px',
                            fontWeight: 'bold'
                        }}>
                            
                        </label>
                        <input
                            type="password"
                            id="password"
                            name="password"
                            value={credentials.password}
                            onChange={handleInputChange}
                            onKeyDown={handleKeyDown}
                            disabled={isLoading}
                            style={{
                                width: '100%',
                                padding: '8px 12px',
                                border: `1px solid ${validationErrors.password ? '#ff6b6b' : '#ddd'}`,
                                borderRadius: '4px',
                                fontSize: '14px',
                                backgroundColor: isLoading ? '#f5f5f5' : 'white',
                            }}
                            placeholder=""
                            autoComplete="current-password"
                        />
                        {validationErrors.password && (
                            <p style={{ color: '#ff6b6b', fontSize: '12px', margin: '4px 0 0 0' }}>
                                {validationErrors.password}
                            </p>
                        )}
                    </div>

                    {/*  */}
                    {error && (
                        <div style={{
                            padding: '12px',
                            marginBottom: '16px',
                            backgroundColor: '#ffebee',
                            border: '1px solid #ffcdd2',
                            borderRadius: '4px',
                            color: '#c62828',
                            fontSize: '14px',
                        }}>
                             {error}
                        </div>
                    )}

                    {/*  */}
                    <button
                        type="submit"
                        disabled={isLoading}
                        style={{
                            width: '100%',
                            padding: '12px',
                            backgroundColor: isLoading ? '#ccc' : '#007bff',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            fontSize: '16px',
                            fontWeight: 'bold',
                            cursor: isLoading ? 'not-allowed' : 'pointer',
                            transition: 'background-color 0.2s',
                        }}
                    >
                        {isLoading ? ' ...' : ''}
                    </button>
                </form>

                {/*  */}
                <div style={{
                    marginTop: '20px',
                    padding: '12px',
                    backgroundColor: '#e8f5e8',
                    border: '1px solid #c8e6c9',
                    borderRadius: '4px',
                    fontSize: '12px',
                }}>
                    <p style={{ margin: '0 0 8px 0', fontWeight: 'bold' }}> :</p>
                    <p style={{ margin: '0' }}>: test_admin</p>
                    <p style={{ margin: '0' }}>: password</p>
                </div>
            </div>
        </div>
    );
};