// src/components/testSuite/CreateTestSuiteModal.tsx

import React, { useState, ChangeEvent, FormEvent } from 'react';
import { useCreateTestSuite } from '../../hooks/useTestSuites';
// Generated
import { CreateTestSuiteInput } from '../../generated/graphql';

interface CreateTestSuiteModalProps {
  onClose: () => void;
  onSuccess: () => void;
}

// 
const formatDateForGraphQL = (dateString: string): string => {
  if (!dateString) return '';

  try {
    // YYYY-MM-DD  ISOYYYY-MM-DDTHH:mm:ss.sssZ
    const date = new Date(dateString + 'T00:00:00.000Z');
    return date.toISOString();
  } catch (error) {
    console.error(':', error);
    return dateString; // 
  }
};

export const CreateTestSuiteModal: React.FC<CreateTestSuiteModalProps> = ({
  onClose,
  onSuccess
}) => {
  const { createTestSuite, loading, error } = useCreateTestSuite();

  // HTML date inputstring
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    estimatedStartDate: '',     // YYYY-MM-DD 
    estimatedEndDate: '',       // YYYY-MM-DD 
    requireEffortComment: false
  });

  const [validationErrors, setValidationErrors] = useState<{
    name?: string;
    estimatedStartDate?: string;
    estimatedEndDate?: string;
  }>({});

  const validateForm = (): boolean => {
    const errors: typeof validationErrors = {};

    if (!formData.name.trim()) {
      errors.name = '';
    }

    if (!formData.estimatedStartDate) {
      errors.estimatedStartDate = '';
    }

    if (!formData.estimatedEndDate) {
      errors.estimatedEndDate = '';
    }

    if (formData.estimatedStartDate && formData.estimatedEndDate) {
      if (new Date(formData.estimatedStartDate) >= new Date(formData.estimatedEndDate)) {
        errors.estimatedEndDate = '';
      }
    }

    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    try {
      // GraphQLISO
      const graphqlInput: CreateTestSuiteInput = {
        name: formData.name.trim(),
        description: formData.description?.trim() || null,
        estimatedStartDate: formatDateForGraphQL(formData.estimatedStartDate), //  ISO
        estimatedEndDate: formatDateForGraphQL(formData.estimatedEndDate),     //  ISO
        requireEffortComment: formData.requireEffortComment || false
      };

      console.log(':', graphqlInput); // 

      await createTestSuite(graphqlInput);
      onSuccess();
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleInputChange = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value, type } = e.target;
    const target = e.target as HTMLInputElement;

    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? target.checked : value
    }));

    // 
    if (validationErrors[name as keyof typeof validationErrors]) {
      setValidationErrors(prev => ({
        ...prev,
        [name]: undefined
      }));
    }
  };

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0,0,0,0.5)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000
    }}>
      <div style={{
        backgroundColor: 'white',
        padding: '24px',
        borderRadius: '8px',
        width: '500px',
        maxWidth: '90vw',
        maxHeight: '90vh',
        overflow: 'auto'
      }}>
        <h3 style={{ margin: '0 0 20px 0' }}></h3>

        <form onSubmit={handleSubmit}>
          {/*  */}
          <div style={{ marginBottom: '16px' }}>
            <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
               <span style={{ color: '#dc3545' }}>*</span>
            </label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleInputChange}
              placeholder=""
              style={{
                width: '100%',
                padding: '8px',
                border: `1px solid ${validationErrors.name ? '#dc3545' : '#ddd'}`,
                borderRadius: '4px'
              }}
            />
            {validationErrors.name && (
              <p style={{ color: '#dc3545', fontSize: '12px', margin: '4px 0 0 0' }}>
                {validationErrors.name}
              </p>
            )}
          </div>

          {/*  */}
          <div style={{ marginBottom: '16px' }}>
            <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
              
            </label>
            <textarea
              name="description"
              value={formData.description || ''}
              onChange={handleInputChange}
              placeholder=""
              rows={3}
              style={{
                width: '100%',
                padding: '8px',
                border: '1px solid #ddd',
                borderRadius: '4px',
                resize: 'vertical'
              }}
            />
          </div>

          {/*  */}
          <div style={{
            display: 'grid',
            gridTemplateColumns: '1fr 1fr',
            gap: '16px',
            marginBottom: '16px'
          }}>
            <div>
              <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
                 <span style={{ color: '#dc3545' }}>*</span>
              </label>
              <input
                type="date"
                name="estimatedStartDate"
                value={formData.estimatedStartDate}
                onChange={handleInputChange}
                style={{
                  width: '100%',
                  padding: '8px',
                  border: `1px solid ${validationErrors.estimatedStartDate ? '#dc3545' : '#ddd'}`,
                  borderRadius: '4px'
                }}
              />
              {validationErrors.estimatedStartDate && (
                <p style={{ color: '#dc3545', fontSize: '12px', margin: '4px 0 0 0' }}>
                  {validationErrors.estimatedStartDate}
                </p>
              )}
            </div>

            <div>
              <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
                 <span style={{ color: '#dc3545' }}>*</span>
              </label>
              <input
                type="date"
                name="estimatedEndDate"
                value={formData.estimatedEndDate}
                onChange={handleInputChange}
                style={{
                  width: '100%',
                  padding: '8px',
                  border: `1px solid ${validationErrors.estimatedEndDate ? '#dc3545' : '#ddd'}`,
                  borderRadius: '4px'
                }}
              />
              {validationErrors.estimatedEndDate && (
                <p style={{ color: '#dc3545', fontSize: '12px', margin: '4px 0 0 0' }}>
                  {validationErrors.estimatedEndDate}
                </p>
              )}
            </div>
          </div>

          {/*  */}
          <div style={{ marginBottom: '20px' }}>
            <label style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
              <input
                type="checkbox"
                name="requireEffortComment"
                checked={formData.requireEffortComment || false}
                onChange={handleInputChange}
              />
              <span style={{ fontWeight: 'bold' }}></span>
            </label>
          </div>

          {/*  */}
          {error && (
            <div style={{
              padding: '12px',
              marginBottom: '16px',
              backgroundColor: '#f8d7da',
              border: '1px solid #f5c6cb',
              borderRadius: '4px',
              color: '#721c24',
              fontSize: '14px'
            }}>
              <strong></strong><br />
              {(error as any)?.message || ''}
            </div>
          )}

          {/*  */}
          <div style={{
            display: 'flex',
            justifyContent: 'flex-end',
            gap: '8px'
          }}>
            <button
              type="button"
              onClick={onClose}
              disabled={loading}
              style={{
                padding: '8px 16px',
                backgroundColor: 'transparent',
                color: '#666',
                border: '1px solid #ddd',
                borderRadius: '4px',
                cursor: loading ? 'not-allowed' : 'pointer'
              }}
            >
              
            </button>

            <button
              type="submit"
              disabled={loading}
              style={{
                padding: '8px 16px',
                backgroundColor: loading ? '#ccc' : '#28a745',
                color: 'white',
                border: 'none',
                borderRadius: '4px',
                cursor: loading ? 'not-allowed' : 'pointer'
              }}
            >
              {loading ? ' ...' : ' '}
            </button>
          </div>
        </form>

        {/*  */}
        {process.env.NODE_ENV === 'development' && (
          <details style={{ marginTop: '16px', fontSize: '12px', color: '#666' }}>
            <summary> </summary>
            <pre style={{ fontSize: '10px', marginTop: '8px' }}>
              {JSON.stringify({
                formData,
                converted: {
                  estimatedStartDate: formatDateForGraphQL(formData.estimatedStartDate),
                  estimatedEndDate: formatDateForGraphQL(formData.estimatedEndDate)
                }
              }, null, 2)}
            </pre>
          </details>
        )}
      </div>
    </div>
  );
};