// src/pages/TestSuiteListPage.tsx - 

import React, { useState } from 'react';
import { useTestSuites, useUpdateTestSuiteStatus } from '../hooks/useTestSuites';
import { TestSuiteList } from '../components/testSuite/TestSuiteList';
import { TestSuiteFilters as FilterModal } from '../components/testSuite/TestSuiteFilters';
import { CreateTestSuiteModal } from '../components/testSuite/CreateTestSuiteModal';
import { MainLayout } from '../layouts/MainLayout';
import { TestSuite, TestSuiteFilters } from '../types/testSuite';
// Generated SuiteStatus
import { SuiteStatus } from '../generated/graphql';

export const TestSuiteListPage: React.FC = () => {
    // 
    const {
        testSuites,
        totalCount,
        loading,
        error,
        filters,
        hasNextPage,
        loadMore,
        updateFilters,
        refresh
    } = useTestSuites({ pageSize: 10 });

    const { updateStatus, loading: statusLoading } = useUpdateTestSuiteStatus();

    // UI
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [showFilters, setShowFilters] = useState(false);

    // 
    const handleStatusChange = async (id: string, status: SuiteStatus) => {
        try {
            await updateStatus(id, status);
            // NotificationContext
            console.log('');
        } catch (error) {
            console.error(':', error);
        }
    };

    const handleFilterChange = (newFilters: Partial<TestSuiteFilters>) => {
        updateFilters(newFilters);
    };

    const handleCreateSuccess = () => {
        setShowCreateModal(false);
        refresh(); // 
    };

    const handleSelectSuite = (suite: TestSuite) => {
        // 
        console.log(':', suite.id);
    };

    // refreshMouseEvent
    const handleRefresh = () => {
        refresh();
    };

    // 
    if (error) {
        return (
            <MainLayout>
                <div style={{
                    padding: '20px',
                    textAlign: 'center',
                    backgroundColor: '#ffebee',
                    border: '1px solid #ffcdd2',
                    borderRadius: '4px',
                    margin: '20px'
                }}>
                    <h3 style={{ color: '#c62828', margin: '0 0 16px 0' }}>
                         
                    </h3>
                    <p style={{ margin: '0 0 16px 0', color: '#666' }}>
                        {error.message || ''}
                    </p>
                    <button
                        onClick={handleRefresh}
                        style={{
                            padding: '8px 16px',
                            backgroundColor: '#007bff',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            cursor: 'pointer'
                        }}
                    >
                        
                    </button>
                </div>
            </MainLayout>
        );
    }

    return (
        <MainLayout>
            <div style={{ padding: '20px' }}>
                {/*  */}
                <div style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginBottom: '20px',
                    paddingBottom: '16px',
                    borderBottom: '1px solid #ddd'
                }}>
                    <div>
                        <h2 style={{ margin: '0 0 8px 0' }}></h2>
                        <p style={{ margin: '0', color: '#666' }}>
                            : {totalCount}
                            {filters.status && ` (${filters.status})`}
                            {filters.search && ` (${filters.search})`}
                        </p>
                    </div>

                    <div style={{ display: 'flex', gap: '12px' }}>
                        <button
                            onClick={() => setShowFilters(true)}
                            style={{
                                padding: '8px 16px',
                                backgroundColor: '#6c757d',
                                color: 'white',
                                border: 'none',
                                borderRadius: '4px',
                                cursor: 'pointer'
                            }}
                        >
                             
                        </button>

                        <button
                            onClick={() => setShowCreateModal(true)}
                            style={{
                                padding: '8px 16px',
                                backgroundColor: '#28a745',
                                color: 'white',
                                border: 'none',
                                borderRadius: '4px',
                                cursor: 'pointer'
                            }}
                        >
                             
                        </button>
                    </div>
                </div>

                {/* Layer 4 */}
                <TestSuiteList
                    testSuites={testSuites}
                    loading={loading}
                    statusLoading={statusLoading}
                    hasNextPage={hasNextPage}
                    onStatusChange={handleStatusChange}
                    onSelectSuite={handleSelectSuite}
                    onLoadMore={loadMore}
                    onRefresh={handleRefresh}
                />

                {/*  */}
                {showCreateModal && (
                    <CreateTestSuiteModal
                        onClose={() => setShowCreateModal(false)}
                        onSuccess={handleCreateSuccess}
                    />
                )}

                {showFilters && (
                    <FilterModal
                        currentFilters={filters}
                        onApply={handleFilterChange}
                        onClose={() => setShowFilters(false)}
                    />
                )}
            </div>
        </MainLayout>
    );
};